    export default {
        name: 'sort-rule',

        props: {
            theme: [String,Object],
			sign: String,
			route: {
            	type: String,
				default: `/pages/search/search`
			}
        },

        data() {
            return {
                sort: 1,
                sort_type: -1,
				loading: false,
				listStyle: false
            }
        },

		computed: {
			classObject: function() {
				return {
					default: this.sort_type === -1,
					tall: this.sort_type === 2,
					low: this.sort_type === 1,
					icon: true,
					'default-background': true
				}
			}
		},
        methods: {
            getFormId(data) {
            },

            setSort(data) {
                this.sort = data;
                if (data !== 3) {
                    this.sort_type = -1;
                } else {
					this.loading = true;
                    switch (this.sort_type) {
                        case -1:
                            this.sort_type = 1;
                            break;
                        case 1:
                            this.sort_type = 2;
                            break;
                        case 2:
                            this.sort_type = 1;
                    }
                }
                this.$emit('sort', {
                    data:data, type: this.sort_type
                });
            },

			setStyle() {
            	this.listStyle = !this.listStyle;
				this.$emit('setStyle', this.listStyle);
			},

			router() {
            	uni.navigateTo({
					url: this.route
				})
			}
        },
    }
